import React from 'react';

const FoldingIcon = ({ node }) => {
    return (
        <g>
            <circle
                cx={node.x + node.width + 5} // Rect
                cy={node.y + node.height / 2} // Rect
                r={4} // 
                fill="none"
                stroke="black"
                strokeWidth="1"
            />
            <text
                x={node.x + node.width + 5} // Rect
                y={node.y + node.height / 2} // Rect
                textAnchor="middle" // 
                dominantBaseline="middle" // 
                fontSize="12" // 
            >
                +
            </text>
        </g>
    );
};

export default FoldingIcon;